import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
@Component({
  selector: 'page-dealerpayment',
  templateUrl: 'dealerpayment.html',
})
export class DealerpaymentPage {

	public dealerCommissionApi = "wp-json/wp/v2/vehicle";
	public dealerData : any={};
  public commission : number = 0;
  public pending : number = 0;
  public rfid : number;
  public refresh : boolean = false;

  constructor(public navCtrl: NavController, public navParams: NavParams,public basic_op : BasicoperationProvider , public httpreq : HttpProvider) {
    
  }

  ionViewDidLoad() {
    this.dealerCommissionApi = this.httpreq.getUrl() + this.dealerCommissionApi;
    
  	this.basic_op.getStorage('rfidno').then(val=>{
              if(val != undefined)
              {
              		this.dealerComm(val);

              }
              else{
              //this.dealerComm(35518163);
           		  this.basic_op.alertboxDismiss("Rfid session not set");         
              }
        });
  }

  	dealerComm(rfid)
  	{
      
  		this.basic_op.loaderStart();
      
  		this.httpreq.filterAcfFields(this.dealerCommissionApi,'rfid',rfid,99).then(res=>{
  			this.dealerData = res;
        console.log(this.dealerData);
          for(let i=0;i<this.dealerData.length;i++)
          {
                if(this.dealerData[i].acf.commission_status == "Yes")
                {
                    console.log(i);  
                  this.commission = parseInt(this.dealerData[i].acf.dealer_commission) + this.commission;

                  console.log(this.dealerData[i].acf.commission_status);
                  console.log(this.commission);
                }
                else if(this.dealerData[i].acf.commission_status == "No"){
                    console.log(i);
                    this.pending = parseInt(this.dealerData[i].acf.dealer_commission) + this.pending;

                    console.log(this.dealerData[i].acf.commission_status);
                  console.log(this.commission);
                }
          }
            this.basic_op.loaderEnd(res);
            this.refresh = true;

  		},err=>{
  				this.basic_op.loaderEnd(err);
  				this.basic_op.alertboxDismiss(err.message);

  		});
  	}

}



