import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams , AlertController } from 'ionic-angular';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { HomePage } from '../../pages/home/home';
import { DealerhomePage } from '../../pages/dealerhome/dealerhome';

@Component({
  selector: 'page-loginpage',
  templateUrl: 'loginpage.html',
})
export class LoginPage {

  public dealerApi = "wp-json/wp/v2/dealer";
  public dealerInfo : any = {};
  public dealer_rfid : any;

  constructor(public navCtrl: NavController,
   public navParams: NavParams,
   private alertCtrl: AlertController,
    public basic_op : BasicoperationProvider,
    public httpreq : HttpProvider) {

  	this.dealerApi = this.httpreq.getUrl() + this.dealerApi;
  }

  ionViewDidLoad() {
  	this.basic_op.sideMenuOpen();
  }
  ionViewWillEnter()
  {
    this.basic_op.sideMenuOpen();
  }

  scanCard()
  {
  	this.navCtrl.push(HomePage);
  }

  manualLogin()
  {

  			let alert = this.alertCtrl.create({
    									title: 'Login',
    									message:'Enter your mobile number',
									    inputs: [
									      {
									        name: 'mobile_no',
									        placeholder: 'Enter Your Mobile Number'
									      }
    										],
									    buttons: [
									      {
									        text: 'Login',
									        handler: data => {
									        		var mobile = data.mobile_no;
                               this.getRfid(mobile);
									        		
									        }
									      }
    									]
  										});
  								alert.present();
  	}

    getRfid(mobile)
    {
      var dealer : any = {};

        this.basic_op.loaderStart();
          this.httpreq.filterAcfFields(this.dealerApi,'mobile_no',mobile).then(res=>{
              this.basic_op.loaderEnd(res);
              dealer = res;
               dealer = dealer[0];

             if(dealer != undefined)
             {  
               if(dealer.acf.mobile_no == mobile)
               {
                   var rfidno = dealer.acf.rfid_no;
                   this.basic_op.setStorage('rfidno',rfidno);
                   this.basic_op.setStorage('dealerid',dealer.id);
                   this.navCtrl.setRoot(DealerhomePage);
               }
               else{
                     this.basic_op.alertboxDismiss("Wrong Mobile No");
               }
            }
            else{
                   this.basic_op.alertboxDismiss("Wrong Mobile No");
               }  
          },error=>{
            this.basic_op.loaderEnd(error.message);
              this.basic_op.alertboxDismiss(error.message);
              
          });
      }

  	// login(rfidno)
   //  {
   //      this.basic_op.loaderStart();
   //        this.httpreq.filterAcfFields(this.dealerApi,'rfid_no',rfidno).then(res=>{
   //            this.basic_op.loaderEnd(res);
   //            this.dealerInfo = res;
   //             this.dealerInfo = this.dealerInfo[0];

   //           if(this.dealerInfo != undefined)
   //           {  
   //             if(this.dealerInfo.acf.rfid_no == rfidno)
   //             {
   //                  this.basic_op.setStorage('rfidno',rfidno);
   //                  this.basic_op.setStorage('dealerid',this.dealerInfo.id);
   //                 	this.navCtrl.setRoot(DealerhomePage);
   //             }
   //             else{
   //             			this.basic_op.alertboxDismiss("Wrong card number");
   //             }
   //          }
   //          else{
   //             		this.basic_op.alertboxDismiss("Wrong card number");
   //             }  
   //        },error=>{
   //        	this.basic_op.loaderEnd(error.message);
   //            this.basic_op.alertboxDismiss(error.message);
              
   //        });
         
   //  }

}



