import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { SocialSharing } from '@ionic-native/social-sharing';
import { ActionSheetController} from 'ionic-angular';

@Injectable()
export class SocialShareProvider {
  baseURL : any;
  constructor(public http: HttpClient,public action : ActionSheetController,public social: SocialSharing) {
  }

  shareWithActionSheet(product)
  {
        const actionSheet = this.action.create({
      title: product.pro_name,
      buttons: [
        {
          text: 'Share on facebook',
          icon:'logo-facebook',
          handler: () => {
            this.social.shareViaFacebook(product.pro_name,product.pro_image,this.baseURL)
          }
        },{
          text: 'Share on Twitter',
          icon:'logo-twitter',
          handler: () => {
             this.social.shareViaTwitter(product.pro_name,product.pro_image,this.baseURL)
          }
          
        },{
          text: 'Share on WhatsApp',
          icon:'logo-whatsapp',
          handler: () => {
             this.social.shareViaWhatsApp(product.pro_name,'',this.baseURL)
          }
         
        },{
          text: 'Share on Instagram',
          icon:'logo-instagram',
          handler: () => {
             this.social.shareViaInstagram(this.baseURL,product.pro_image)
          }
           
        },{
          text: 'Share',
          icon:'ios-share-alt',
          handler: () => {
             this.social.share('',product.pro_name,'',this.baseURL)
          }
           
        },{
          text: 'Cancel',
          icon:'logo-destructive',
          handler: () => {}
         
        }
      ]
    });
    actionSheet.present();
  }

  shareWithFacebookTwitter(product)
  {
    this.social.shareViaFacebook(product.pro_name,product.pro_image,this.baseURL);
  }

  shareWithWhatsapp(product)
  {
    this.social.shareViaWhatsApp(product.pro_name,'',this.baseURL)
  }

  shareWithInstagram(product)
  {
    this.social.shareViaInstagram(this.baseURL,product.pro_image)
  }

  share(product)
  {
    this.social.share('',product.pro_name,'',this.baseURL)
  }

}



