import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams , AlertController } from 'ionic-angular';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';

import { ReportPage } from '../../pages/report/report';


@Component({
  selector: 'page-paymentmode-summary',
  templateUrl: 'paymentmode-summary.html',
})
export class PaymentmodeSummaryPage {

	public dealerCustomerApi : any = "wp-json/wp/v2/dealer_customer";

	public payment : any = [];
	public paymentData : any = [];
  constructor(public navCtrl: NavController,
   public navParams: NavParams,
    public httpreq : HttpProvider,
   public basic_op : BasicoperationProvider,
   private alertCtrl: AlertController) {
    this.dealerCustomerApi = this.httpreq.getUrl() + this.dealerCustomerApi;
    this.payment=this.navParams.get("data");
  }

  ionViewDidLoad() {

  }
  editpayment()
  {
  	this.navCtrl.pop();
  }

  reportpage()
  {
  		this.httpreq.send(this.payment,this.dealerCustomerApi).then(res=>{this.basic_op.loaderEnd(res);this.paymentData = res;
       		this.navCtrl.push(ReportPage,{rfid : this.paymentData.acf.rfid_no,customer_id : this.paymentData.acf.customer_id,vehicle_id : this.paymentData.acf.vehicle_id});
      },error => {
        this.basic_op.alertboxDismiss(error.message);
        this.basic_op.loaderEnd(error);
        
      });
  }
}



