import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams , Platform } from 'ionic-angular';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { DomSanitizer } from '@angular/platform-browser';

import { DealerpaymentPage } from '../../pages/dealerpayment/dealerpayment';
import pdfMake from 'pdfmake/build/pdfmake';
import pdfFonts from 'pdfmake/build/vfs_fonts';
pdfMake.vfs = pdfFonts.pdfMake.vfs;
 
import { File } from '@ionic-native/file';
import { FileOpener } from '@ionic-native/file-opener';

@Component({
  selector: 'page-dealerprofile',
  templateUrl: 'dealerprofile.html',
})
export class DealerprofilePage {

	public dealerApi = "wp-json/wp/v2/dealer";
  public dealerInfo : any = [];
  public rfidno :any ;

  pdfObj = null;

  constructor(public navCtrl: NavController, public navParams: NavParams,public basic_op : BasicoperationProvider , public httpreq : HttpProvider,private file: File,
    private fileOpener: FileOpener,
     private plt: Platform) {
    this.dealerApi = this.httpreq.getUrl() + this.dealerApi;
  }

  ionViewDidLoad() {
  	this.basic_op.getStorage('rfidno').then(val=>{
              if(val)
              {
              		this.dealerProfile(val);

              }
              else{
           		  this.basic_op.alertboxDismiss("rfid session not set");         
              }
        });
  }

  dealerProfile(rfid)
    {
    	this.basic_op.loaderStart();

        this.httpreq.filterAcfFields(this.dealerApi,'rfid_no',rfid,1).then(res=>{
            this.dealerInfo = res;this.dealerInfo = this.dealerInfo[0];this.dealerInfo = this.dealerInfo.acf;this.basic_op.loaderEnd(res);
            console.log(this.dealerInfo);
        },error=>{
          this.basic_op.alertboxDismiss(error.message);
        });
    }

    dealerCommission()
    {
        this.navCtrl.push(DealerpaymentPage);
    }

    reportPage()
    {
          this.downloadPdf();
    }

    downloadPdf()
    {
        this.createPdf();

        if (this.plt.is('cordova')) {
      this.pdfObj.getBuffer((buffer) => {
        var blob = new Blob([buffer], { type: 'application/pdf' });
 
        // Save the PDF to the data Directory of our App
        this.file.writeFile(this.file.dataDirectory,this.dealerInfo.dealer_name+'.pdf', blob, { replace: true }).then(fileEntry => {
          // Open the PDf with the correct OS tools
          this.fileOpener.open(this.file.dataDirectory+this.dealerInfo.dealer_name+'.pdf', 'application/pdf');
        })
      });
    } else {
      // On a browser simply use download!
      this.pdfObj.download();
    }
    }

    createPdf() {

      console.log(this.dealerInfo);
   
    var docDefinition = {
      content: [

        { text: 'Dealer Details', style: 'header' },
        { text: 'Rfid No', style: 'label' },
        { text: this.dealerInfo.rfid_no , style: 'subheader'},
        { text: 'Name', style: 'label' },
        { text: this.dealerInfo.dealer_name , style: 'subheader'},
        { text: 'Mobile No', style: 'label' },
        { text: this.dealerInfo.mobile_no , style: 'subheader'},
        { text: 'Area', style: 'label' },
        { text: this.dealerInfo.area , style: 'subheader'},
        { text: 'Address', style: 'label' },
        { text:  this.dealerInfo.address , style: 'subheader'},
        { text: 'Company', style: 'label' },
        { text: this.dealerInfo.company , style: 'subheader'},
        
      ],
      styles: {
        header: {
          fontSize: 15,
          bold: true,
        },
        subheader: {
          fontSize: 11,
          bold: true,
          alignment: 'right',
        },
        label: {
          fontSize: 10,
          alignment: 'left'
        },
      }
    }
    this.pdfObj = pdfMake.createPdf(docDefinition);
  }

}



