import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams , ModalController} from 'ionic-angular';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { CustomerslistPage } from '../../pages/customerslist/customerslist';
import { DealerformPage } from '../../pages/dealerform/dealerform';

@Component({
  selector: 'page-dealerslist',
  templateUrl: 'dealerslist.html',
})
export class DealerslistPage {
	
	public dealerApi = "wp-json/wp/v2/dealer_posts";
    public dealerInfo : any= [];
    public company : any;

  constructor(public navCtrl: NavController,
   public navParams: NavParams,
   public basic_op : BasicoperationProvider,
   public httpreq : HttpProvider,public modalCtrl : ModalController) {
   	this.dealerApi = this.httpreq.getUrl() + this.dealerApi;
  }

  ionViewDidLoad() {

  	this.basic_op.getStorage('admin').then(val1=>{
              if(val1 != undefined)
              {
                 this.company = val1;
                 this.dealerDetails();
              }
              else{

                  if(this.company != undefined)
                  {
                     this.basic_op.alertboxDismiss("Admin session not set");   
                   }      
              }
        });
  }

  dealerDetails()
  {
      console.log(this.company);
      this.basic_op.loaderStart();
        this.httpreq.getAllDataPosts(this.dealerApi).then(res=>{
            this.basic_op.loaderEnd(res);
            this.dealerInfo = res;
            this.dealerInfo = this.dealerInfo.data;            
            console.log(this.dealerInfo);
        },err=>{
            this.basic_op.loaderEnd(err);
            this.basic_op.alertboxDismiss(err.message);
        });
  }

  customerlist(rfid)
  {
  		this.navCtrl.push(CustomerslistPage,{rfid:rfid});
  }

  editDealer(dealerid)
  {
      let dealer = this.modalCtrl.create(DealerformPage, {dealerid:dealerid});
       dealer.present();
       dealer.onDidDismiss(()=>{
        this.dealerDetails();
       });
  }

}



