import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams , AlertController} from 'ionic-angular';
import { Serial } from '@ionic-native/serial';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { DealerformPage } from '../../pages/dealerform/dealerform';
import { LoginPage } from '../../pages/loginpage/loginpage';

@Component({
  selector: 'page-dealerscan',
  templateUrl: 'dealerscan.html',
})
export class DealerscanPage {

  public dealerApi : any = "wp-json/wp/v2/dealer";
  public rfid : string = "";
  public device_attach : boolean;
  public dealerInfo : any = {};

  constructor(public navCtrl: NavController,
  	public navParams: NavParams,
  	public basic_op : BasicoperationProvider ,
  	 public httpreq : HttpProvider ,
  	 private serial: Serial,
  	 public alertCtrl : AlertController
  	) {
    this.dealerApi = this.httpreq.getUrl() + this.dealerApi;
  }

  ionViewDidLoad() {
  	//this.cardChecker();
  }

  ionViewWillEnter()
  {
  	this.rfid = "";
    this.cardChecker();
  }
  cardChecker()
   {
   		this.basic_op.removeStorage('rfidno');
   		this.basic_op.removeStorage('dealerid');

    this.serial.requestPermission().then(()=>{
    this.serial.open({baudRate: 9600,
    dataBits: 8,
    stopBits: 1,
    parity: 0,
    dtr: true,
    rts: true,
    sleepOnPause: false}).then(()=>{

        this.device_attach = true;

        this.serial.registerReadCallback().subscribe(res=>{

           var r = new Uint8Array(res);

           this.rfid = this.rfid.concat(String.fromCharCode.apply(null,r));

           if(this.rfid.length == 8)
           {
                     this.rfid = this.rfid.substring(0,8);
                     // this.serial.close();
                     this.checkDealerRegister();
                     
           }
         },error=>{
           this.basic_op.alertboxDismiss("rfid card not read");
         });

        },error=>{
          this.basic_op.alertboxDismiss("connection problem issue");
        });
    },error=>{
      let alert = this.alertCtrl.create({
            title: 'Machine is not connected',
            message : 'Machine is not connected.You have to connect the machine then dismiss.', 
            buttons: [
                        {
                            text: 'Dismiss',
                            handler: () => {
                                       if(this.device_attach){
                                           // this.basic_op.alertboxDismiss("Machine is connected.");
                                           //this.cardChecker();
                                       } 
                                       else{
                                         this.cardChecker();  
                                       }
                                    }
                        }
                    ],
                    enableBackdropDismiss : false
                });
                        alert.present();
             });
       
       }

       checkDealerRegister()
       {
           this.basic_op.loaderStart();
          this.httpreq.filterAcfFields(this.dealerApi,'rfid_no',this.rfid).then(res=>{
              this.basic_op.loaderEnd(res);
              this.dealerInfo = res;
               this.dealerInfo = this.dealerInfo[0];

             if(this.dealerInfo != undefined)
             {  
               if(this.dealerInfo.acf.rfid_no == this.rfid)
               {
                     let alert = this.alertCtrl.create({
                      title: 'Card already registered',
                      buttons: [
                        {
                          text: 'Dismiss',
                          handler: data => {
                              this.navCtrl.setRoot(LoginPage);
                          }
                        }
                      ],
                      enableBackdropDismiss : false
                      });
                  alert.present();          
                }
               else{
                     this.serial.close();
                     this.navCtrl.push(DealerformPage,{rfid : this.rfid});
               }
            }
            else{
                    this.serial.close();
                   this.navCtrl.push(DealerformPage,{rfid : this.rfid});
               }  
          },error=>{
            this.basic_op.loaderEnd(error.message);
              this.basic_op.alertboxDismiss(error.message);
              
          });
       }
}



