import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { HomePage } from '../../pages/home/home';

import { DealerprofilePage } from '../../pages/dealerprofile/dealerprofile';
import { CustomerformPage } from '../../pages/customerform/customerform';
import { CustomerdetailsPage } from '../../pages/customerdetails/customerdetails';
import { DealerpaymentPage } from '../../pages/dealerpayment/dealerpayment';
import { LoginPage } from '../../pages/loginpage/loginpage';

@Component({
  selector: 'page-dealerhome',
  templateUrl: 'dealerhome.html',
})
export class DealerhomePage {

  constructor(public navCtrl: NavController, public navParams: NavParams ,public basic_op : BasicoperationProvider , public httpreq : HttpProvider ) {
    this.basic_op.sideMenuClose();
  }

  ionViewDidLoad() {
  }


  logout()
  {
        this.basic_op.removeStorage('rfidno');
        this.basic_op.removeStorage('dealerid');
        this.navCtrl.setRoot(LoginPage);
  }

  dealerProfile()
  {
      this.navCtrl.push(DealerprofilePage);
  }
    
   addCustomer()
   {
       this.navCtrl.push(CustomerformPage);
   }

    customerDetails()
    {
        this.navCtrl.push(CustomerdetailsPage);
    }
    dealerPayment()
    {
        this.navCtrl.push(DealerpaymentPage);
    }
}



