import { HttpClient , HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';

@Injectable()
export class HttpProvider {

  data: any = null;
  TokenApi = "wp-json/wp/v2/pages/2";
  url: string = 'http://sandipmistry.com/pushpam-moters/'
  apiData : any;
  token : any ;
  constructor(public http: HttpClient,public storage: Storage) {
      this.http.get(this.url+this.TokenApi).subscribe(val=>{
          this.apiData = val;
          console.log(this.apiData);
           return this.token = this.apiData.acf.token_no; 
    });
  }

  getUrl() {
    return this.url;
  }

  getJWTAuth(){
     return "Bearer " + this.token;
   }

  get( route, page= "1" ) {

    return new Promise( (resolve, reject) => {

      if( !route )
        reject({ data: { message: "No URL set. " } })

      var concat;
      // check if url already has a query param
      if( route.indexOf('?') > 0 ) {
        concat = '&';
      } else {
        concat = '?';
      }

      //let url = this.url + route;
      let url = route;
    //  set pagination
      if( page === 'nopaging' ) {
        // get all results with no paging
        url = url + concat + 'per_page=50'
      } else {
        url = url + concat + 'page=' + page
      }

      this.http.get(url)
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
         },
        error => {
           reject(error);
          this.handleError(error)
        })
    }); 
  }  

  getAuthenticatedData(route, page= "1")
  {
      return new Promise( (resolve, reject) => {

      if( !route )
        reject({ data: { message: "No URL set. " } })

      var concat;
      // check if url already has a query param
      if( route.indexOf('?') > 0 ) {
        concat = '&';
      } else {
        concat = '?';
      }

      //let url = this.url + route;
      let url = route;
    //  set pagination
      if( page === 'nopaging' ) {
        // get all results with no paging
        url = url + concat + 'per_page=50'
      } else {
        url = url + concat + 'page=' + page
      }


      const httpOptions = {
        headers: new HttpHeaders({
          'Authorization': this.getJWTAuth()
        })
      };

      this.http.get(url,httpOptions)
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        },
        error => {
           reject(error);
          this.handleError(error)
        })
    });
  }

  send(data,route) {

    return new Promise( (resolve, reject) => {

      if( !data )
        reject({ data: { message: "No data." } })

      let url = route

      const httpOptions = {
        headers: new HttpHeaders({
          'Content-Type':  'application/json',
          'Authorization': this.getJWTAuth()
        })
      };

      this.http.post( url, data, httpOptions)
        .subscribe(data => {

          this.data = data;

          resolve(this.data);
        },
        error => {
          // probably a bad url or 404
          reject(error);
          this.handleError(error)
        })

    }); // end Promise

  }


  delete(route,Id)
  {
    return new Promise( (resolve, reject) => {

      if( !Id )
        reject({ data: { message: "No Id." } })

      let url = route+"/"+Id+"?force=true";
      console.log(url);

      const httpOptions = {
        headers: new HttpHeaders({
          'Content-Type':  'application/json',
          'Authorization': this.getJWTAuth()
        })
      };

      this.http.delete(url,httpOptions)
        .subscribe(data => {

          this.data = data;

          resolve(this.data);
        },
        error => {
          // probably a bad url or 404
          reject(error);
          this.handleError(error)
        })

    });
  }

  update(data,id,route)
  {
      return new Promise( (resolve, reject) => {

      if( !data )
        reject({ data: { message: "No data." } })

      let url = route +"/"+id; 

      const httpOptions = {
        headers: new HttpHeaders({
          'Content-Type':  'application/json',
          'Authorization': this.getJWTAuth()
        })
      };

      this.http.put( url, data, httpOptions)
        .subscribe(data => {

          this.data = data;

          resolve(this.data);
        },
        error => {
          // probably a bad url or 404
          reject(error);
          this.handleError(error)
        })

    }); 
  }
  getOnedata(route,Id)
  {
       return new Promise( (resolve, reject) => {

      if( !route )
        reject({ data: { message: "No URL set. " } })

      let url = route+"/"+Id;
      
      console.log(url);
     
      this.http.get(url)
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        },
        error => {
           reject(error);
          this.handleError(error)
        })
    });   
  }


  getAllData(route)
  {
       return new Promise( (resolve, reject) => {

      if( !route )
        reject({ data: { message: "No URL set. " } })

      let url = route+"/?per_page=100";
      
      console.log(url);
     
      this.http.get(url)
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        },
        error => {
           reject(error);
          this.handleError(error)
        })
    });

  }


  handleError(err) {
    console.warn(err);
  }


  getPerPageData(route,record)
  {
       return new Promise( (resolve, reject) => {

      if( !route )
        reject({ data: { message: "No URL set. " } })

      let url = route+"/?per_page="+record;
      
      console.log(url);
     
      this.http.get(url)
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        },
        error => {
           reject(error);
          this.handleError(error)
        })
    });
     }


     filterCategoriesDataById(route,record,id)
     {
            return new Promise( (resolve, reject) => {

      if( !route )
        reject({ data: { message: "No URL set. " } })

      let url = route+"?filter[cat]="+id+"&per_page="+record;
      
      console.log(url);
     
      this.http.get(url)
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        },
        error => {
           reject(error);
          this.handleError(error)
        })
    });
     }

       filterCategoriesDataByName(route,record,name)
     {
            return new Promise( (resolve, reject) => {

      if( !route )
        reject({ data: { message: "No URL set. " } })

      let url = route+"?filter[category_name]="+name+"&per_page="+record;
      
      console.log(url);
     
      this.http.get(url)
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        },
        error => {
           reject(error);
          this.handleError(error)
        })
    });
     }
  
    //install wp-rest-filter  plugin by SK8Tech

    filterAcfFields(route,key,value,record=1)
    {
         return new Promise( (resolve, reject) => {

      if( !route )
        reject({ data: { message: "No URL set. " } })

      let url = route+"?filter[meta_key]="+key+"&filter[meta_value]="+value+"&per_page="+record;
      
      console.log(url);
     
      this.http.get(url)
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        },
        error => {
           reject(error);
          this.handleError(error)
        })
        });
    }

    filterAcfFieldsAllrecord(route,key,value)
    {
         return new Promise( (resolve, reject) => {

      if( !route )
        reject({ data: { message: "No URL set. " } })

      let url = route+"?filter[meta_key]="+key+"&filter[meta_value]="+value;
      
      console.log(url);
     
      this.http.get(url)
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        },
        error => {
           reject(error);
          this.handleError(error)
        })
        });
    }

    filterDataByOrder(route,record,field,order=0)
    {
        var direction : string;

        if(order == 1)
        {
            direction = 'desc';
        }
        else{
            direction = 'asc';
        }
        return new Promise( (resolve, reject) => {

      if( !route )
        reject({ data: { message: "No URL set. " } })

      let url = route+"?filter[orderby]="+field+"&order="+direction+"&per_page="+record;
      
      console.log(url);
     
      this.http.get(url)
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        },
        error => {
           reject(error);
          this.handleError(error)
        })
    });
    }

    filterAcfDataByOrder(route,record,field,order=0)
    {
           var direction : string;

        if(order == 1)
        {
            direction = 'desc';
        }
        else{
            direction = 'asc';
        }
        return new Promise( (resolve, reject) => {

      if( !route )
        reject({ data: { message: "No URL set. " } })

      let url = route+"?filter[orderby]=meta_value_num&filter[meta_key]="+field+"&filter[order]="+direction+"&per_page="+record;
      
      console.log(url);
     
      this.http.get(url)
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        },
        error => {
           reject(error);
          this.handleError(error)
        })
    });

    }

    filterById(route,id)
    {
         return new Promise( (resolve, reject) => {

      if( !route )
        reject({ data: { message: "No URL set. " } })

      let url = route+"/"+id;
      
      console.log(url);
     
      this.http.get(url)
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        },
        error => {
           reject(error);
          this.handleError(error)
        })
    });
    }

    getAllDataPosts(route)
    {
         return new Promise( (resolve, reject) => {

      if( !route )
        reject({ data: { message: "No URL set. " } })

      //let url = route+"/?per_page=100";
      
      console.log(route);
     
      this.http.get(route)
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        },
        error => {
           reject(error);
          this.handleError(error)
        })
    });
    }

    // search(val,data)
    // {
    //     return data.filter(ev =>{
    //        (ev.toLowerCase().indexOf(val.toLowerCase()) > -1);
    //     });
    // }
}



