import { Component, ViewChild } from '@angular/core';
import { Nav, Platform , Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { BasicoperationProvider } from '../providers/basicoperation/basicoperation';
import { HttpProvider } from '../providers/httpprovider/httpprovider';
import { NetworkProvider } from '../providers/network/network';
import { Network } from '@ionic-native/network';
import { ReportPage } from '../pages/report/report';
import { DealerscanPage } from '../pages/dealerscan/dealerscan';

import { HomePage } from '../pages/home/home';
import { DealerformPage } from '../pages/dealerform/dealerform';
import { CustomerformPage } from '../pages/customerform/customerform';
import { CustomerdetailsPage } from '../pages/customerdetails/customerdetails';
import { AdminloginPage } from '../pages/adminlogin/adminlogin';
import { DealerhomePage } from '../pages/dealerhome/dealerhome';
import { DealerslistPage } from '../pages/dealerslist/dealerslist';
import { CustomerslistPage } from '../pages/customerslist/customerslist';
import { DealerprofilePage } from '../pages/dealerprofile/dealerprofile';
import { CustomersummaryPage } from '../pages/customersummary/customersummary';
import { VehicleformPage } from '../pages/vehicleform/vehicleform';
import { DealerpaymentPage } from '../pages/dealerpayment/dealerpayment';
import { LoginPage } from '../pages/loginpage/loginpage';


@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  //rootPage: any = AdminloginPage;
  
  rootPage : any;

  pages: Array<{title: string, component: any}>;

  constructor(public platform: Platform,
   public statusBar: StatusBar,
    public splashScreen: SplashScreen,
    public basic_op : BasicoperationProvider ,
     public httpreq : HttpProvider,
     public networkProvider: NetworkProvider,
      public events: Events,
      public network: Network,) {
    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [
      { title: 'Home', component: LoginPage },
      { title: 'New Dealer', component: DealerscanPage },
      { title: 'Dealers List', component: DealerslistPage },
      { title: 'Customers List', component: CustomerslistPage },
    ];

  }

  initializeApp() {
    this.platform.ready().then(() => {

       //this.networkProvider.initializeNetworkEvents();

             // Offline event
          // this.events.subscribe('network:offline', () => {
          //     this.basic_op.alertboxDismiss('network:offline ==> '+this.network.type);    
          // });

          // Online event
          // this.events.subscribe('network:online', () => {
          //     this.basic_op.alertboxDismiss('network:online ==> '+this.network.type);        
          // });

      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();


        this.basic_op.removeStorage('rfidno');
        this.basic_op.removeStorage('dealerid');
        
        this.basic_op.getStorage('admin').then(val=>{
              if(val){
                  this.rootPage = LoginPage;
              }
              else{
                this.rootPage = AdminloginPage;
              }
        });


    });
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }

  logout()
  {
      this.basic_op.removeStorage('admin');
      this.nav.setRoot(AdminloginPage);
  }
}



