import { HttpClient , HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AlertController , LoadingController , ToastController , MenuController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { Network } from '@ionic-native/network';


@Injectable()
export class BasicoperationProvider {
  public loading : any;
  constructor(public http: HttpClient , public alertCtrl : AlertController,public loadingCtrl: LoadingController , private toastCtrl: ToastController,public storage : Storage , public menuCtrl : MenuController,public network: Network,) {
    
  }

  	alertboxDismiss(title,btn_text='Dismiss',subtitle = '') {

              let alert = this.alertCtrl.create({
                title: title,
              subTitle: subtitle,
                buttons: [btn_text],
                enableBackdropDismiss : false
              });

              alert.present();
          }

  alertboxConfirm(title,message,fun_submit,btn_cancel="Cancel",btn_submit="Agree"){
             let alert = this.alertCtrl.create({
                  title: title,
                  message: message,
                  buttons: [{
                            text: btn_cancel,
                            handler: () => {}
                            },
                        {
                           text: btn_submit,
                          handler: () => {
                                    fun_submit();}
                     }]
              });
                alert.present();}

     
    loaderStart(content='')
    {
        this.loading = this.loadingCtrl.create({
         // spinner : 'hide',
          // content:'<img src="../assets/vehicle/logo.jpg">'
          });

            this.loading.present();
    }

    loaderEnd(response)
    {
        if(response)
    {
        this.loading.dismiss();
    }    
    else{
       this.loading.dismiss();
       this.alertboxDismiss("Can't getting any kind of response.");
    }
    }

  toast(message,duration=2000,position="bottom") {
              let toast = this.toastCtrl.create({
                message: message,
              duration: duration,
                position: position
              });
                toast.present();
          }

  setStorage(name,data)
  {
      this.storage.set(name,data);
  }

  getStorage(name)
  {
     return this.storage.get(name);
  }

  removeStorage(name)
  {
      this.storage.ready().then(() => {
                          this.storage.remove(name);
                          });
  }

  sideMenuClose()
  {
        this.menuCtrl.enable(false,'myMenu');
  }

  sideMenuOpen()
  {
        this.menuCtrl.enable(true,'myMenu');
  }

   networkConnect()
   {
        this.network.onConnect().subscribe(()=>{
           this.alertboxDismiss("Network is connected");
        });
    }

    networkDisConnect()
    {
       this.network.onDisconnect().subscribe(()=>{
           this.alertboxDismiss("Network is failed");
        }); 
    }
}



