import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams , Platform } from 'ionic-angular';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';

import { VehicleformPage } from '../../pages/vehicleform/vehicleform';
import { ModalController } from 'ionic-angular';

import pdfMake from 'pdfmake/build/pdfmake';
import pdfFonts from 'pdfmake/build/vfs_fonts';
pdfMake.vfs = pdfFonts.pdfMake.vfs;
 
import { File } from '@ionic-native/file';
import { FileOpener } from '@ionic-native/file-opener';

@Component({
  selector: 'page-vehicledetails',
  templateUrl: 'vehicledetails.html',
})
export class VehicledetailsPage {
   public customerApi : any = "wp-json/wp/v2/customer";
	public vehicleApi = "wp-json/wp/v2/vehicle";

	public vehicleinfo : any = [];
  public customerInfo : any = [];

	public customerid : any;

  pdfObj = null;

  constructor(public navCtrl: NavController,
   public navParams: NavParams,
   public httpreq : HttpProvider,
   public basic_op : BasicoperationProvider,
   public modalCtrl: ModalController,
   private file: File,
    private fileOpener: FileOpener,
     private plt: Platform) {
  }

  ionViewDidLoad() {
    this.vehicleApi = this.httpreq.getUrl() + this.vehicleApi;
    this.customerApi = this.httpreq.getUrl() + this.customerApi;
 	this.customerid = this.navParams.get('customerId');
 	this.vehicleDetails();
   this.customerDetails();
  }

  	vehicleDetails()
  	{
      this.basic_op.loaderStart();
  		this.httpreq.filterAcfFields(this.vehicleApi,'customer_id',this.customerid,99).then(res=>{this.basic_op.loaderEnd(res);
         this.vehicleinfo = res;console.log(this.vehicleinfo);
        },err=>{
          this.basic_op.loaderEnd(err);
            this.basic_op.alertboxDismiss(err.message);
        });
  	}

    customerDetails()
    {  
        this.httpreq.getOnedata(this.customerApi,this.customerid).then(res=>{
            this.customerInfo = res; 
            this.customerInfo = this.customerInfo.acf;           
            console.log(this.customerInfo);
        },err=>{
            this.basic_op.alertboxDismiss(err.message);
        });
    }

  	editVehicle(vehicle)
  	{
       let vehicleModal = this.modalCtrl.create(VehicleformPage, {vehicle_id:vehicle});
       vehicleModal.present();
       vehicleModal.onDidDismiss(()=>{
         this.vehicleDetails();
       });
  	}

    reportPage(vehicleInfo)
    {
          this.downloadPdf(vehicleInfo);
    }

    downloadPdf(vehicleInfo)
    {
        this.createPdf(vehicleInfo);

        if (this.plt.is('cordova')) {
      this.pdfObj.getBuffer((buffer) => {
        var blob = new Blob([buffer], { type: 'application/pdf' });
 
        // Save the PDF to the data Directory of our App
        this.file.writeFile(this.file.dataDirectory,this.customerInfo.customer_name+"-"+vehicleInfo.acf.vehicle_name+'.pdf', blob, { replace: true }).then(fileEntry => {
          // Open the PDf with the correct OS tools
          this.fileOpener.open(this.file.dataDirectory+this.customerInfo.customer_name+"-"+vehicleInfo.acf.vehicle_name+'.pdf', 'application/pdf');
        })
      });
    } else {
      this.pdfObj.download();
    }

    }

    createPdf(vehicleInfo) {

    var docDefinition = {
      content: [

       { text: 'Customer Details', style: 'header' },
       // { text: new Date().toTimeString(), alignment: 'right' },
 
        //{ text: 'From', style: 'subheader' },
        { text: 'Customer Name', style: 'label' },
        { text: this.customerInfo.customer_name , style: 'subheader'},
        { text: 'Area', style: 'label' },
        { text: this.customerInfo.area , style: 'subheader'},
        { text: 'Address', style: 'label' },
        { text: this.customerInfo.address , style: 'subheader'},
        { text: 'Mobile No', style: 'label' },
        { text: this.customerInfo.mobile_no , style: 'subheader'},


        { text: 'Vehicle Details', style: 'header' },

        { text: 'Chalan No', style: 'label' },
        { text: vehicleInfo.acf.chalan_no , style: 'subheader'},
        { text: 'Engine No', style: 'label' },
        { text: vehicleInfo.acf.engine_no , style: 'subheader'},
        { text: 'Model No', style: 'label' },
        { text: vehicleInfo.acf.model_no , style: 'subheader'},
        { text: 'Frame No', style: 'label' },
        { text: vehicleInfo.acf.frame_no , style: 'subheader'},
        { text: 'Vehicle Price', style: 'label' },
        { text: vehicleInfo.acf.vehicle_price , style: 'subheader'},
        { text: 'Vehicle Pending Price', style: 'label' },
        { text: vehicleInfo.acf.pending_price , style: 'subheader'},
        { text: 'Insaurance Policy', style: 'label' },
        { text: vehicleInfo.acf.insaurance_policy , style: 'subheader'},
        { text: 'Purchase Date', style: 'label' },
        { text: vehicleInfo.acf.purchase_date , style: 'subheader'},
        { text: 'Payment Mode', style: 'label' },
        { text: vehicleInfo.acf.payment_mode , style: 'subheader'},
        { text: 'Company', style: 'label' },
        { text: vehicleInfo.acf.company , style: 'subheader'},
        
      ],
      styles: {
        header: {
          fontSize: 15,
          bold: true,
        },
        subheader: {
          fontSize: 11,
          bold: true,
          alignment: 'right',
        },
        label: {
          fontSize: 10,
          alignment: 'left'
        },
      }
    }
    this.pdfObj = pdfMake.createPdf(docDefinition);
  }
}



