import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams , normalizeURL , ViewController } from 'ionic-angular';

import { ActionSheetController } from 'ionic-angular';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';
import { CameraProvider } from '../../providers/camera/camera';

import { DealersummaryPage } from '../../pages/dealersummary/dealersummary';


@Component({
  selector: 'page-dealerform',
  templateUrl: 'dealerform.html',
})
export class DealerformPage {

	public dealerApi : any = "wp-json/wp/v2/dealer";
  public mediaApi = "wp-json/wp/v2/media";

  public dealerid : any;
	public userData : any = [];
	public AddChosenPicture : any = [];
	public dealerPic : any = [];
	public chosenPicture : any = [];
	public identityPic : any = [];
 // public rfid_no : string = "";

  constructor(public navCtrl: NavController,
   public navParams: NavParams,
   public httpreq : HttpProvider,
   public basic_op : BasicoperationProvider,
   public cameraProvider : CameraProvider,
   public actionSheetCtrl: ActionSheetController,
   public viewCtrl : ViewController) {

    this.dealerApi = this.httpreq.getUrl() + this.dealerApi;

    this.userData.rfid_no = this.navParams.get("rfid");
    this.dealerid = this.navParams.get("dealerid");
    if(this.dealerid != undefined)
     {
         this.httpreq.getOnedata(this.dealerApi,this.dealerid).then(res=>{
           this.userData = res;this.userData = this.userData.acf;console.log(this.userData.id_proof);
           this.chosenPicture.length = 1;
           this.AddChosenPicture.length = 1;
         });
     }
  }

  ionViewDidLoad() { }

  addDealer()
  {

    if(this.dealerid != undefined)
    {

       var data1 = {
      "title" : this.userData.rfid_no,
      "status" : "publish",
      "fields" : {

                "rfid_no": this.userData.rfid_no,
                "dealer_name": this.userData.dealer_name,
                "area": this.userData.area,
                "address": this.userData.address,
                "mobile_no": this.userData.mobile_no,
                "company": this.userData.company,
                
      }
    }
           this.basic_op.loaderStart();
      this.httpreq.update(data1,this.dealerid,this.dealerApi).then(res=>{
        this.basic_op.loaderEnd(res);

        console.log(res);this.viewCtrl.dismiss();
      },
      err=>{
        this.basic_op.loaderEnd(err);
        this.basic_op.alertboxDismiss(err.message);
      });
    } 
    else
    {

  		 this.basic_op.loaderStart();

    if(this.dealerPic.length > 0)
    {
        this.httpreq.delete(this.mediaApi ,this.dealerPic);
        this.httpreq.delete(this.mediaApi ,this.identityPic);
    }

        if(this.chosenPicture.length > 0 && this.AddChosenPicture.length > 0)
        {
              this.insert();
        }
        else if(this.chosenPicture.length > 0 && this.AddChosenPicture.length == 0)
        {
              this.cameraProvider.upload(this.chosenPicture).then(res => {
           this.dealerPic = res;this.basic_op.loaderEnd(this.dealerPic);
           this.dealerPic = this.dealerPic.response;
           this.dealerPic = JSON.parse(this.dealerPic);
           this.dealerPic = this.dealerPic.id;

       
    var data = {
      "title" : this.userData.rfid_no,
      "status" : "publish",
      "fields" : {

                "rfid_no": this.userData.rfid_no,
                "dealer_name": this.userData.dealer_name,
                "area": this.userData.area,
                "address": this.userData.address,
                "mobile_no": this.userData.mobile_no,
                "company": this.userData.company,
                "id_proof":this.dealerPic
      }
    }

    var photos = {
      "photo" : "../../assets/vehicle/profile.png",
      "identity" : this.chosenPicture
     }

      this.navCtrl.push(DealersummaryPage ,{data : data , photos : photos});

      },error =>{
        this.basic_op.loaderEnd(error);
        this.basic_op.alertboxDismiss(error.message);
      });


        }
        else if(this.chosenPicture.length == 0 && this.AddChosenPicture.length > 0)
        {
          
            this.cameraProvider.upload(this.AddChosenPicture).then(res => {
            this.identityPic = res;this.basic_op.loaderEnd(this.identityPic);
            this.identityPic = this.identityPic.response;
           this.identityPic = JSON.parse(this.identityPic);
           this.identityPic = this.identityPic.id;


    var data = {
      "title" : this.userData.rfid_no,
      "status" : "publish",
      "fields" : {

                "rfid_no": this.userData.rfid_no,
                "dealer_name": this.userData.dealer_name,
                "area": this.userData.area,
                "address": this.userData.address,
                "mobile_no": this.userData.mobile_no,
                "company": this.userData.company,
                "dealer_photo":this.identityPic
          }
    }

    var photos = {
      "photo" : this.AddChosenPicture,
      "identity" : "../../assets/vehicle/document.png"
     }

      this.navCtrl.push(DealersummaryPage ,{data : data , photos : photos});

      },error =>{
        this.basic_op.loaderEnd(error);
        this.basic_op.alertboxDismiss(error.message);
      });
          
        }  
        else{
     
            var data = {
                "title" : this.userData.rfid_no,
                "status" : "publish",
                "fields" : {

                          "rfid_no": this.userData.rfid_no,
                          "dealer_name": this.userData.dealer_name,
                          "area": this.userData.area,
                          "address": this.userData.address,
                          "mobile_no": this.userData.mobile_no,
                          "company": this.userData.company,
                }
            }
            this.basic_op.loaderEnd(data);


     var photos = {
      "photo" : "../../assets/vehicle/profile.png",
      "identity" : "../../assets/vehicle/document.png"
     }

     this.navCtrl.push(DealersummaryPage ,{data : data , photos : photos});

     }



    } 

  }

  insert()
  {
      this.cameraProvider.upload(this.chosenPicture).then(res => {
           this.dealerPic = res;
           this.dealerPic = this.dealerPic.response;
           this.dealerPic = JSON.parse(this.dealerPic);
           this.dealerPic = this.dealerPic.id;

            this.cameraProvider.upload(this.AddChosenPicture).then(res => {
            this.identityPic = res;this.basic_op.loaderEnd(this.identityPic);
            this.identityPic = this.identityPic.response;
           this.identityPic = JSON.parse(this.identityPic);
           this.identityPic = this.identityPic.id;


    var data = {
      "title" : this.userData.rfid_no,
      "status" : "publish",
      "fields" : {

                "rfid_no": this.userData.rfid_no,
                "dealer_name": this.userData.dealer_name,
                "area": this.userData.area,
                "address": this.userData.address,
                "mobile_no": this.userData.mobile_no,
                "company": this.userData.company,
                "dealer_photo":this.identityPic,
                "id_proof":this.dealerPic
      }
    }

    var photos = {
      "photo" : this.AddChosenPicture,
      "identity" : this.chosenPicture
    }

      this.navCtrl.push(DealersummaryPage ,{data : data , photos : photos});

      },error =>{
        this.basic_op.loaderEnd(error);
        this.basic_op.alertboxDismiss(error.message);
      });

    },error => {
      this.basic_op.loaderEnd(error);
      this.basic_op.alertboxDismiss(error.message);
    });
  }

  onOptionsSelected(e)
  {
  		console.log(e);
  }

  uplodPictureAdd() {

    const actionsheet = this.actionSheetCtrl.create({
      title: 'Upload Picture',
      buttons: [
        {
          text: 'Camera',
          handler: () => {
            this.dealerTakePicture();
          }
        },
        {
          text: 'Image Gallery',
          handler: () => {
            this.dealerGetPicture();
          }
        },
        {
          text: 'Cancel',
          role: 'destructive',
          handler: () => {
            console.log('the user has cancelled the interaction.');
          }
        }
      ]
    });
    return actionsheet.present();
  }

   dealerTakePicture() {

    this.basic_op.loaderStart();

    return this.cameraProvider.getProfilePictureFromCamera().then(picture => {this.basic_op.loaderEnd(picture);
      if (picture) {
        return this.AddChosenPicture = normalizeURL(picture);
          
      }
      
    }, error => {
      this.basic_op.loaderEnd(error);
      this.basic_op.alertboxDismiss(error.message);
    });

  }

  dealerGetPicture() {

    this.basic_op.loaderStart();

    return this.cameraProvider.getPictureFromPhotoLibrary().then(picture => {this.basic_op.loaderEnd(picture);
      if (picture) {
        return this.AddChosenPicture = normalizeURL(picture);
          
      }
      
    }, error => {
      this.basic_op.loaderEnd(error);
      this.basic_op.alertboxDismiss(error.message);
    });

  }


  uplodPicture() {
    const actionsheet = this.actionSheetCtrl.create({
      title: 'Upload Picture',
      buttons: [
        {
          text: 'Camera',
          handler: () => {
            this.takePicture();
          }
        },
        {
          text: 'Image Gallery',
          handler: () => {
            this.getPicture();
          }
        },
        {
          text: 'Cancel',
          role: 'destructive',
          handler: () => {
            console.log('the user has cancelled the interaction.');
          }
        }
      ]
    });
    return actionsheet.present();
  }

  takePicture() {

    this.basic_op.loaderStart();

    return this.cameraProvider.getProfilePictureFromCamera().then(picture => {this.basic_op.loaderEnd(picture);
      if (picture) {
        return this.chosenPicture = picture;

      }
      
    }, error => {

    });
  }

  getPicture() {
      this.basic_op.loaderStart();

    return this.cameraProvider.getPictureFromPhotoLibrary().then(picture => {this.basic_op.loaderEnd(picture);
      if (picture) {
         return this.chosenPicture = normalizeURL(picture);
      }
      this.basic_op.loaderEnd(picture);
    }, error => {
      //alert(error);
    });
  }

  modalClose()
  {
    this.viewCtrl.dismiss();
  }

}



