import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';

import { PaymentmodeSummaryPage } from '../../pages/paymentmode-summary/paymentmode-summary';

@Component({
  selector: 'page-dealer-customerform',
  templateUrl: 'dealer-customerform.html',
})
export class DealerCustomerformPage {

	public dealerCustomerApi : any = "wp-json/wp/v2/dealer_customer";
	public dealer_customer : any = [];
	public tdate = new Date;
	public entry_date : any ;
  public customer_id : number;
  public vehicle_id : number;
  public rfid : any;
  constructor(public navCtrl: NavController,
   public navParams: NavParams,
   public httpreq : HttpProvider,
   public basic_op : BasicoperationProvider) {

    this.dealerCustomerApi = this.httpreq.getUrl() + this.dealerCustomerApi;
    this.vehicle_id = this.navParams.get("vehicle_id");
     this.customer_id = this.navParams.get("customer_id");
      this.basic_op.getStorage('rfidno').then(res=>{
        this.rfid = res;
        return  this.rfid;
     },
     err=>{
       this.basic_op.alertboxDismiss(err.message);
     });
  }

  ionViewDidLoad() {
  	this.entry_date = this.tdate.getDate()+'/'+(this.tdate.getMonth() + 1)+'/'+this.tdate.getFullYear();
  	this.entry_date = this.entry_date.toString();
  }

   addDealerCustomer()
  {
  		// this.basic_op.loaderStart();

      
    var data = {
      "title" : this.rfid +"-" +this.customer_id,
      "status" : "publish",
      "fields" : {

      			    "rfid_no": this.rfid,
                "customer_id": this.customer_id,
                "entry_date": this.entry_date,
                "payment_mode": this.dealer_customer.payment,
                "dealer_commission": this.dealer_customer.commission,
                "commission_status":this.dealer_customer.commission_status,
                "vehicle_id": this.vehicle_id
               
    	      }
	    }
        this.navCtrl.push(PaymentmodeSummaryPage,{data : data});
  }

  onOptionsSelected(e)
  {
  		console.log(e);
  }
}



