import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ActionSheet, ActionSheetOptions } from '@ionic-native/action-sheet';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';


@Injectable()
export class ControllersProvider {

  constructor(public http: HttpClient,public actionSheet: ActionSheet, public basicOperation : BasicoperationProvider) {
  }

   actionSheetControllers()
  {
    try
    {

      const options: ActionSheetOptions = {
            title: 'Upload Picture',
            subtitle: 'Choose an picture',
            buttonLabels: ['Camera', 'Image Gallery'],
             addCancelButtonWithLabel: 'Cancel',
             addDestructiveButtonWithLabel: 'Delete',
            destructiveButtonLast: true
        }
   
      this.actionSheet.show(options).then((buttonIndex: number) => {
        this.basicOperation.alertboxDismiss(buttonIndex);
        switch (buttonIndex) {
          case 1:
              
            break;
          case 2:
            break;
        }
                      
      });

   }catch(e)
   {
     console.error("error :- ",e);
   }   
  }
}


