import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { HttpProvider } from '../httpprovider/httpprovider';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';


@Injectable()
export class CameraProvider {

  data: any = null;
  sourceType:any;
  destinationType:any;
  uploadURL:any;
  url : any;
  imageData : any;
  constructor(public http: HttpClient ,public httpprovider : HttpProvider,public camera: Camera,
    public transfer: FileTransfer,public basic_op : BasicoperationProvider) {

        this.url = this.httpprovider.getUrl()+"wp-json/wp/v2/media";
        console.log(this.url);
  }

  getPictureFromCamera() {
    return this.getImage(this.camera.PictureSourceType.CAMERA, false);
  }
  getProfilePictureFromCamera() {
    return this.getImage(this.camera.PictureSourceType.CAMERA, true);
  }

  getPictureFromPhotoLibrary() {
    return this.getImage(this.camera.PictureSourceType.PHOTOLIBRARY);
  }

  getImage(pictureSourceType, crop = false, quality = 20, allowEdit = true, saveToAlbum = false) {
    const options = {
      quality : quality,
      allowEdit: allowEdit,
      destinationType: this.camera.DestinationType.DATA_URL,
      sourceType: pictureSourceType,
      encodingType: this.camera.EncodingType.JPEG,
      saveToPhotoAlbum: saveToAlbum,
      correctOrientation:true
    };
   

    return this.camera.getPicture(options).then(imageData => {
      const base64Image = 'data:image/jpeg;base64,' + imageData;
      //this.upload(base64Image)
      return base64Image;
    }, error => {
      console.log('CAMERA ERROR -> ' + JSON.stringify(error));
    });
  }
  

  upload(image) {

    let fileTransfer: FileTransferObject = this.transfer.create();

    let options: FileUploadOptions = {
       headers:{
         'Authorization': this.httpprovider.getJWTAuth(),
         'content-disposition':"attachment; filename=\'yash.jpeg\'"
       }
    }

     return fileTransfer.upload(image, this.url , options)
    .then((data) => {
      return data
    }, (err) => {
     console.log(err)
    })

  }

}



