import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';
import { DealerCustomerformPage } from '../../pages/dealer-customerform/dealer-customerform';
import { ReportPage } from '../../pages/report/report';

@Component({
  selector: 'page-vehiclesummary',
  templateUrl: 'vehiclesummary.html',
})
export class VehiclesummaryPage {

	public vehicleApi : any = "wp-json/wp/v2/vehicle";
	public vehicleInfo : any = [];
  public vehicle : any;
  public rfid : any;
  public customerId : any ;
  constructor(public navCtrl: NavController,
   public navParams: NavParams,
    public httpreq : HttpProvider,
   public basic_op : BasicoperationProvider) {
    this.vehicleApi = this.httpreq.getUrl() + this.vehicleApi;
   	this.vehicleInfo = this.navParams.get('data');
     this.customerId = this.navParams.get('customer_id');

     this.basic_op.getStorage('rfidno').then(res=>{
       if(res != undefined)
       {
          return this.rfid = res;
       }
       else{
           this.basic_op.alertboxDismiss("Rfid session not set");
           //return this.rfid = 831;
       }
     },
     err=>{
       this.basic_op.alertboxDismiss(err.message);
     });
  }

  ionViewDidLoad() {
   
  }

  editVehicle()
  {
  	this.navCtrl.pop();
  }

  // addPaymentMode()
  // {
  // 		this.httpreq.send(this.vehicleInfo,this.vehicleApi).then(res=>{this.basic_op.loaderEnd(res);this.vehicle = res;
  //      this.navCtrl.push(DealerCustomerformPage,{vehicle_id : this.vehicle.id,customer_id : this.vehicle.acf.customer_id});
  //     },error => {
  //       this.basic_op.alertboxDismiss(error.message);
       
  //     });
  	
  // }

  reportpage()
  {
    this.basic_op.loaderStart();
    
      this.httpreq.send(this.vehicleInfo,this.vehicleApi).then(res=>{this.basic_op.loaderEnd(res);this.vehicle = res;
           this.navCtrl.push(ReportPage,{rfid : this.rfid,customer_id : this.customerId,vehicle_id : this.vehicle.id});
      },error => {
        this.basic_op.alertboxDismiss(error.message);
        this.basic_op.loaderEnd(error);
        
      });
  }
}



