import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { HomePage } from '../../pages/home/home';
import { LoginPage } from '../../pages/loginpage/loginpage';

@Component({
  selector: 'page-adminlogin',
  templateUrl: 'adminlogin.html',
})
export class AdminloginPage {

	public adminApi : any = "wp-json/wp/v2/admin";
	public loginData : any={};
  public adminData : any = [];
  public url : any ;
  constructor(public navCtrl: NavController, public navParams: NavParams , public basic_op : BasicoperationProvider , public httpreq : HttpProvider) {
    

    this.url = this.httpreq.getUrl();
    this.adminApi = this.url + this.adminApi;
  }

  ionViewDidLoad() {

  }

  userLogin(){

      this.basic_op.loaderStart();
      this.httpreq.filterAcfFields(this.adminApi,'admin_name',this.loginData.username,1).then(res=>{this.basic_op.loaderEnd(res);this.adminData = res;this.adminData = this.adminData[0];

              if(this.adminData != undefined)
              {
                if(this.adminData.acf.admin_name == this.loginData.username)
                {
                     if(this.adminData.acf.password == this.loginData.password)
                     {
                           this.basic_op.setStorage('admin',this.adminData.title.rendered);
                           this.navCtrl.setRoot(LoginPage);
                     }
                     else{
                       this.basic_op.alertboxDismiss('Wrong Username or Password');
                     }
                }
                else{
                      this.basic_op.alertboxDismiss('Wrong Username or Password');
                }
              }
              else{
                    this.basic_op.alertboxDismiss('Wrong Username or Password');
              }
      },error=>{
            this.basic_op.alertboxDismiss("error :- "+error.message);
      });
  }

}



