import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams , normalizeURL , ViewController} from 'ionic-angular';
import { ActionSheetController } from 'ionic-angular';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';
import { CameraProvider } from '../../providers/camera/camera';

import { CustomersummaryPage } from '../../pages/customersummary/customersummary';


@Component({
  selector: 'page-customerform',
  templateUrl: 'customerform.html',
})
export class CustomerformPage {

  public customerApi = "wp-json/wp/v2/customer";
  public mediaApi = "wp-json/wp/v2/media";

  public userData : any = [];
  public AddChosenPicture : any = [];
  public customerPic : any = [];
  public chosenPicture : any = [];
  public identityPic : any = [];
  public rfid : any;
  public customerid : any;
  public admin : any;

  constructor(public navCtrl: NavController,
   public navParams: NavParams,
   public httpreq : HttpProvider,
   public basic_op : BasicoperationProvider,
   public cameraProvider : CameraProvider,
   public actionSheetCtrl: ActionSheetController,
   public viewCtrl : ViewController) {

    this.mediaApi = this.httpreq.getUrl() + this.mediaApi;
    this.rfid = this.navParams.get("dealerid");

     this.basic_op.getStorage('dealerid').then(res=>{
         if(res != undefined)
         {
             this.rfid = res;
             return  this.rfid;
         }
         else{
               if(!this.rfid)
               {
                 this.basic_op.alertboxDismiss(" Rfid session not set");  
               }
               
         }
        
     },
     err=>{
       this.basic_op.alertboxDismiss(err.message);
     });

     this.basic_op.getStorage('admin').then(adm=>{
         if(adm != undefined)
         {
             this.admin = adm;
             return  this.admin;
         }
         else{
               if(!this.admin)
               {
                 this.basic_op.alertboxDismiss(" Rfid session not set");  
               }
               
         }
        
     },
     err=>{
       this.basic_op.alertboxDismiss(err.message);
     });


     this.customerid = this.navParams.get("customer_id");
     
     
     if(this.customerid != undefined)
     {
         this.customerApi = this.httpreq.getUrl() + this.customerApi;
         this.httpreq.getOnedata(this.customerApi,this.customerid).then(res=>{
           this.userData = res; this.userData = this.userData.acf;
           this.chosenPicture.length = 1;
           this.AddChosenPicture.length = 1;
         });
     }
       
  }

   ionViewWillEnter() {
   
   }

  addCustomer()
  {

    if(this.customerid != undefined)
    {
        var data={
                "title" : this.userData.customer_name,
                "status" : "publish",
                 "fields" : {
                              "customer_name": this.userData.customer_name,
                              "area": this.userData.area,
                              "address": this.userData.address,
                              "mobile_no": this.userData.mobile_no,
                            }    
                  }

           this.basic_op.loaderStart();
      this.httpreq.update(data,this.customerid,this.customerApi).then(res=>{
        this.basic_op.loaderEnd(res);
        console.log(res);this.viewCtrl.dismiss();
      },
      err=>{
        this.basic_op.loaderEnd(err);
        this.basic_op.alertboxDismiss(err.message);
      });
    } 
    else
    {
        this.basic_op.loaderStart();
    if(this.customerPic.length > 0)
    {
        this.httpreq.delete(this.mediaApi ,this.customerPic);
        this.httpreq.delete(this.mediaApi ,this.identityPic);
    }

       this.cameraProvider.upload(this.chosenPicture).then(res => {
           this.customerPic = res;
            this.customerPic = this.customerPic.response;
           this.customerPic = JSON.parse(this.customerPic);
           this.customerPic = this.customerPic.id;

            this.cameraProvider.upload(this.AddChosenPicture).then(res => {
             this.basic_op.loaderEnd(res);
           this.identityPic = res;
          this.identityPic = this.identityPic.response;
           this.identityPic = JSON.parse(this.identityPic);
           this.identityPic = this.identityPic.id;

    var data = {
      "title" : this.userData.customer_name,
      "status" : "publish",
      "fields" : {

                "customer_name": this.userData.customer_name,
                "area": this.userData.area,
                "address": this.userData.address,
                "mobile_no": this.userData.mobile_no,
                "id_proof_front": this.identityPic,
                "id_proof_back": this.customerPic,
                "company": this.admin,
                "dealer_id":this.rfid
      }
    }

    var photos = {
      "photo" : this.AddChosenPicture,
      "identity" : this.chosenPicture
    }
   
        this.navCtrl.push(CustomersummaryPage ,{data : data , photos : photos});

      },error =>{
        this.basic_op.loaderEnd(error);
        this.basic_op.alertboxDismiss(error.message);
      });
    },error => {
      this.basic_op.loaderEnd(error);
      this.basic_op.alertboxDismiss(error.message);
    });
   }    

  }


  onOptionsSelected(e)
  {
      console.log(e);
  }

  uplodPictureAdd() {

    const actionsheet = this.actionSheetCtrl.create({
      title: 'Select Picture',
      buttons: [
        {
          text: 'Camera',
          handler: () => {
            this.dealerTakePicture();
          }
        },
        {
          text: 'Image Gallery',
          handler: () => {
            this.dealerGetPicture();
          }
        },
        {
          text: 'Cancel',
          role: 'destructive',
          handler: () => {
            console.log('the user has cancelled the interaction.');
          }
        }
      ]
    });
    return actionsheet.present();
  }

   dealerTakePicture() {

    this.basic_op.loaderStart();

    return this.cameraProvider.getProfilePictureFromCamera().then(picture => {this.basic_op.loaderEnd(picture);
      if (picture) {
        return this.AddChosenPicture = normalizeURL(picture);
          
      }
      
    }, error => {
      this.basic_op.loaderEnd(error);
      this.basic_op.alertboxDismiss(error.message);
    });

  }

  dealerGetPicture() {

    this.basic_op.loaderStart();

    return this.cameraProvider.getPictureFromPhotoLibrary().then(picture => {this.basic_op.loaderEnd(picture);
      if (picture) {
        return this.AddChosenPicture = normalizeURL(picture);
          
      }
      
    }, error => {
      this.basic_op.loaderEnd(error);
      this.basic_op.alertboxDismiss(error.message);
    });

  }


  uplodPicture() {
    const actionsheet = this.actionSheetCtrl.create({
      title: 'Select Picture',
      buttons: [
        {
          text: 'Camera',
          handler: () => {
            this.takePicture();
          }
        },
        {
          text: 'Image Gallery',
          handler: () => {
            this.getPicture();
          }
        },
        {
          text: 'Cancel',
          role: 'destructive',
          handler: () => {
            console.log('the user has cancelled the interaction.');
          }
        }
      ]
    });
    return actionsheet.present();
  }

  takePicture() {

    this.basic_op.loaderStart();

    return this.cameraProvider.getProfilePictureFromCamera().then(picture => {this.basic_op.loaderEnd(picture);
      if (picture) {
        return this.chosenPicture = normalizeURL(picture);

      }
      
    }, error => {

    });
  }

  getPicture() {
      this.basic_op.loaderStart();

    return this.cameraProvider.getPictureFromPhotoLibrary().then(picture => {this.basic_op.loaderEnd(picture);
      if (picture) {
         return this.chosenPicture = normalizeURL(picture);
      }
      this.basic_op.loaderEnd(picture);
    }, error => {
      //alert(error);
    });
  }

  modalClose()
  {
    this.viewCtrl.dismiss();
  }
}



