import { Component} from '@angular/core';
import { NavController,Platform,AlertController,MenuController} from 'ionic-angular';
import { Serial } from '@ionic-native/serial';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { ControllersProvider } from '../../providers/controllers/controllers';
import { AndroidPermissions } from '@ionic-native/android-permissions';

import { DealerhomePage } from '../../pages/dealerhome/dealerhome';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  public dealerApi = "wp-json/wp/v2/dealer";
  public dealerInfo : any = {};
  public rfidno :any ;

	public rfid : string = "";
  public device_attach : boolean;

  constructor(public navCtrl: NavController , public basic_op : BasicoperationProvider , public httpreq : HttpProvider , public ctrlProvider : ControllersProvider ,private serial: Serial,private androidPermissions: AndroidPermissions,public platform : Platform,private alertCtrl: AlertController, public menuCtrl : MenuController) {

    this.dealerApi = this.httpreq.getUrl() + this.dealerApi;  
    this.basic_op.sideMenuClose();    
   } 

   ionViewDidLoad(){
     this.cardChecker();
   }
   cardChecker()
   {
        this.basic_op.getStorage('rfidno').then(val=>{

          if(val == undefined)
          {
       //{vid:'2341',pid:'0043',driver:'FtdiSerialDriver'}
       this.rfid = "";
    this.serial.requestPermission().then(()=>{
    this.serial.open({baudRate: 9600,
    dataBits: 8,
    stopBits: 1,
    parity: 0,
    dtr: true,
    rts: true,
    sleepOnPause: false}).then(()=>{

        this.device_attach = true;

        this.serial.registerReadCallback().subscribe(res=>{

           var r = new Uint8Array(res);

           this.rfid = this.rfid.concat(String.fromCharCode.apply(null,r));

           if(this.rfid.length == 8)
           {
                     this.rfid = this.rfid.substring(0,8);
                     // this.serial.close();
                    this.login(this.rfid);
                    this.serial.close();
           }
         },error=>{
           this.basic_op.alertboxDismiss("rfid card not read");
         });

        },error=>{
          this.basic_op.alertboxDismiss("connection problem issue");
        });
    },error=>{
      let alert = this.alertCtrl.create({
            title: 'Machine is not connected',
            message : 'Machine is not connected.You have to connect the machine then dismiss.', 
            buttons: [
                        {
                            text: 'Dismiss',
                            handler: () => {
                                       if(this.device_attach){
                                           this.basic_op.alertboxDismiss("Machine is connected.");
                                       } 
                                       else{
                                         this.cardChecker();  
                                       }
                                    }
                        }
                    ],
                    enableBackdropDismiss : false
                });
                        alert.present();
             });
          }
        },err=>{
       });

       }

    login(rfidno)
    {
        //this.basic_op.loaderStart();

        this.rfid = "";        
          this.httpreq.filterAcfFields(this.dealerApi,'rfid_no',rfidno).then(res=>{
             // this.basic_op.loaderEnd(res);
              this.dealerInfo = res;
               this.dealerInfo = this.dealerInfo[0];

             if(this.dealerInfo != undefined)
             {  
               if(this.dealerInfo.acf.rfid_no == rfidno)
               {
                    this.basic_op.setStorage('rfidno',rfidno);
                    this.basic_op.setStorage('dealerid',this.dealerInfo.id);
                     this.navCtrl.setRoot(DealerhomePage);
               }
               else{

                   this.rfid = "";
                //   this.basic_op.alertboxDismiss("Card not detected.Try again");
                //   let alert = this.alertCtrl.create({
                //     title: 'Card not detected.Try again', 
                //     buttons: [
                //         {
                //             text: 'Dismiss',
                //             handler: () => {
                //                        this.rfid = "";
                //                        this.dealerInfo.length = 0;
                //                        this.cardChecker();
                                                             
                //                     }
                //         }
                //     ],
                //     enableBackdropDismiss : false
                // });

               }
            }
            else{
                  this.rfid = "";
                //   this.basic_op.alertboxDismiss("Card not detected.Try again");
                //   let alert = this.alertCtrl.create({
                //     title: 'Card not detected.Try again', 
                //     buttons: [
                //         {
                //             text: 'Dismiss',
                //             handler: () => {
                //                        this.rfid = "";
                //                        this.dealerInfo.length = 0;
                //                        this.cardChecker();
                                                             
                //                     }
                //         }
                //     ],
                //     enableBackdropDismiss : false
                // });
               }  
          },error=>{
              this.basic_op.alertboxDismiss("Card not detected.Try again");
              this.cardChecker();
          });
         
    }
}



