import { Component  } from '@angular/core';
import { IonicPage, NavController, NavParams , AlertController , Platform } from 'ionic-angular';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';
import { VehicledetailsPage } from '../../pages/vehicledetails/vehicledetails';
import { CustomerformPage } from '../../pages/customerform/customerform';

import { ModalController } from 'ionic-angular';

import pdfMake from 'pdfmake/build/pdfmake';
import pdfFonts from 'pdfmake/build/vfs_fonts';
pdfMake.vfs = pdfFonts.pdfMake.vfs;
 
import { File } from '@ionic-native/file';
import { FileOpener } from '@ionic-native/file-opener';
@Component({
  selector: 'page-customerdetails',
  templateUrl: 'customerdetails.html',
})
export class CustomerdetailsPage {

	public customerApi = "wp-json/wp/v2/customer";
	public customerinfo : any = [];
  
  pdfObj = null;
  constructor(public navCtrl: NavController,
   public navParams: NavParams,
   public httpreq : HttpProvider,
   public basic_op : BasicoperationProvider,
   public alert : AlertController,
   public modalCtrl: ModalController,
   private file: File,
    private fileOpener: FileOpener,
     private plt: Platform) {
    this.customerApi = this.httpreq.getUrl() + this.customerApi;
  }

  ionViewDidLoad() {
    this.basic_op.getStorage('dealerid').then(val=>{
              if(val != undefined)
              {
                  this.customerDetails(val);
              }
              else{
                
                 this.basic_op.alertboxDismiss("Session is not set");         
              }
        });
  }

  	customerDetails(dealerid)
  	{
        this.basic_op.loaderStart();
        this.httpreq.filterAcfFields(this.customerApi,'dealer_id',dealerid,99).then(res=>{
            this.basic_op.loaderEnd(res);
            this.customerinfo = res;            
            console.log(this.customerinfo);
        },err=>{
            this.basic_op.loaderEnd(err);
            this.basic_op.alertboxDismiss(err.message);
        });
  	}

    vehicleDetails(customerid)
    {
        this.navCtrl.push(VehicledetailsPage, {customerId: customerid});
         
    }

    editCustomer(customerId)
    {
      let customer = this.modalCtrl.create(CustomerformPage, {customer_id:customerId});
       customer.present();
       customer.onDidDismiss(()=>{
         this.basic_op.getStorage('dealerid').then(val=>{
              if(val != undefined)
              {
                  this.customerDetails(val);

              }
              else{

                  this.customerDetails(val);
               }
               this.basic_op.loaderEnd(val);
          });
       });
    }

    reportPage(customerInfo)
    {
          this.downloadPdf(customerInfo);
    }

    downloadPdf(customerInfo)
    {
        this.createPdf(customerInfo);

        if (this.plt.is('cordova')) {
      this.pdfObj.getBuffer((buffer) => {
        var blob = new Blob([buffer], { type: 'application/pdf' });
 
        // Save the PDF to the data Directory of our App
        this.file.writeFile(this.file.dataDirectory,customerInfo.acf.customer_name+'.pdf', blob, { replace: true }).then(fileEntry => {
          // Open the PDf with the correct OS tools
          this.fileOpener.open(this.file.dataDirectory+customerInfo.acf.customer_name+'.pdf', 'application/pdf');
        })
      });
    } else {
      // On a browser simply use download!
      this.pdfObj.download();
    }
    }

    createPdf(customerInfo) {

      console.log(customerInfo);
   
    var docDefinition = {
      content: [

        { text: 'Customer Details', style: 'header' },
        { text: 'Customer Name', style: 'label' },
        { text: customerInfo.acf.customer_name , style: 'subheader'},
        { text: 'Area', style: 'label' },
        { text: customerInfo.acf.area , style: 'subheader'},
        { text: 'Address', style: 'label' },
        { text:  customerInfo.acf.address , style: 'subheader'},
        { text: 'Mobile No', style: 'label' },
        { text: customerInfo.acf.mobile_no , style: 'subheader'},
        
      ],
      styles: {
        header: {
          fontSize: 15,
          bold: true,
        },
        subheader: {
          fontSize: 11,
          bold: true,
          alignment: 'right',
        },
        label: {
          fontSize: 10,
          alignment: 'left'
        },
      }
    }
    this.pdfObj = pdfMake.createPdf(docDefinition);
  }


}



