import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';

import { VehicleformPage } from '../../pages/vehicleform/vehicleform';

@Component({
  selector: 'page-customersummary',
  templateUrl: 'customersummary.html',
})
export class CustomersummaryPage {

	public customerApi : any = "wp-json/wp/v2/customer";
	public customerInfo : any = [];
	public customerPhotos : any = [];
  public customerAdd : any = [];

  constructor(public navCtrl: NavController,
   public navParams: NavParams,
    public httpreq : HttpProvider,
   public basic_op : BasicoperationProvider) {
    this.customerApi = this.httpreq.getUrl() + this.customerApi;
   this.customerInfo = this.navParams.get('data');
   this.customerPhotos = this.navParams.get('photos');
  }

  ionViewDidLoad() {
  }

  editCustomer()
  {
  		this.navCtrl.pop();
  }

  addVehicle()
  {
  		this.basic_op.loaderStart();
  		this.httpreq.send(this.customerInfo,this.customerApi).then(res=>{this.basic_op.loaderEnd(res);this.customerAdd = res;
  			this.navCtrl.push(VehicleformPage,{ customer_id : this.customerAdd.id});
          
      },error => {
        this.basic_op.alertboxDismiss(error.message);
        this.basic_op.loaderEnd(error);
        
      });
  		
  }

}



