import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams , ViewController } from 'ionic-angular';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';
import { CameraProvider } from '../../providers/camera/camera';

import { VehiclesummaryPage } from '../../pages/vehiclesummary/vehiclesummary';


@Component({
  selector: 'page-vehicleform',
  templateUrl: 'vehicleform.html',
})
export class VehicleformPage {

	public vehicleApi : any = "wp-json/wp/v2/vehicle";
  public modelApi : any = "wp-json/wp/v2/model";
	public vehicleData : any = [];
  public tdate = new Date;
  public entry_date : any ;
  public customer_id : number;
  public vehicle_id : any;
  public rfid : any;
  public company : any ;
  public modeldata : any;
  public model_no : any;
  constructor(public navCtrl: NavController,
   public navParams: NavParams,
   public httpreq : HttpProvider,
   public basic_op : BasicoperationProvider,
   public cameraProvider : CameraProvider,
   public viewCtrl : ViewController) {

    this.vehicleApi = this.httpreq.getUrl() + this.vehicleApi;
    this.modelApi = this.httpreq.getUrl() + this.modelApi;
    this.customer_id = this.navParams.get("customer_id");
    this.entry_date = this.tdate.getDate()+'/'+(this.tdate.getMonth() + 1 )+'/'+this.tdate.getFullYear();
    this.entry_date = this.entry_date.toString();

    this.rfid = this.navParams.get("rfid");
   
  }

  ionViewDidLoad() {
    
      
      this.vehicle_id = this.navParams.get("vehicle_id");
        if(this.vehicle_id != undefined)
        {
            this.httpreq.getOnedata(this.vehicleApi,this.vehicle_id).then(res=>{
                this.vehicleData = res;this.vehicleData = this.vehicleData.acf;this.model_no = this.vehicleData.model_no
            },err=>{

            });
        }

        this.basic_op.getStorage('rfidno').then(val=>{
              if(val != undefined)
              {
                 this.rfid = val;
              }
              else{
                //  this.rfid = 35518163;
                  if(this.rfid != undefined)
                  {
                     this.basic_op.alertboxDismiss("Rfid session not set");   
                   }      
              }
        });

        this.basic_op.getStorage('admin').then(val1=>{
              if(val1 != undefined)
              {
                 this.company = val1;
                this.getModel();
              }
              else{
                  // return this.rfid = 35518163;
                  if(this.company != undefined)
                  {
                     this.basic_op.alertboxDismiss("Admin session not set");   
                   }      
              }
        });
  }

  getModel()
  {
    this.httpreq.filterAcfFields(this.modelApi,'company',this.company,99).then(res=>{
      this.modeldata = res;console.log(this.modeldata);
    });
  }

   addVehicle()
  {
    if(this.vehicle_id != undefined)
    {

        var data1 = {
      "title" : this.vehicleData.chalan_no,
      "status" : "publish",
      "fields" : {

                "chalan_no":this.vehicleData.chalan_no,
                "engine_no":this.vehicleData.engine_no,
                "model_no":this.vehicleData.model_no,
                "frame_no": this.vehicleData.frame_no,
                "vehicle_price": this.vehicleData.vehicle_price,
                "pending_price": this.vehicleData.vehicle_pending_price,
                "insaurance_policy": this.vehicleData.insaurance_policy,
                "purchase_date": this.entry_date,
                "payment_mode": this.vehicleData.payment_mode,
                "dealer_commission": this.vehicleData.dealer_commission,
                "commission_status":this.vehicleData.commission_status,
      }
    }
         this.basic_op.loaderStart();
      this.httpreq.update(data1,this.vehicle_id,this.vehicleApi).then(res=>{
        this.basic_op.loaderEnd(res);
        console.log(res);this.viewCtrl.dismiss();
      },
      err=>{
        this.basic_op.loaderEnd(err);
        this.basic_op.alertboxDismiss(err.message);
      });

    }	
    else{

    var data2 = {
      "title" : this.vehicleData.chalan_no,
      "status" : "publish",
      "fields" : {
                "chalan_no":this.vehicleData.chalan_no,
                "engine_no":this.vehicleData.engine_no,
                "model_no":this.model_no,
                "frame_no": this.vehicleData.frame_no,
                "vehicle_price": this.vehicleData.vehicle_price,
                "pending_price": this.vehicleData.vehicle_pending_price,
                "insaurance_policy": this.vehicleData.insaurance_policy,
                "purchase_date": this.entry_date,
                "payment_mode": this.vehicleData.payment_mode,
                "dealer_commission": this.vehicleData.dealer_commission,
                "commission_status":this.vehicleData.commission_status,
                "vehicle_photo": "",
                "customer_id" : this.customer_id,
                "rfid" : this.rfid,
                "company": this.company
    	}
	  }
    this.navCtrl.push(VehiclesummaryPage ,{data : data2 , customer_id : this.customer_id});
   }

  }

  onOptionsSelectedd(e)
  {
      console.log(e);
  }

  onOptionsSelected(e)
  {
      this.model_no = e;
      console.log(this.model_no);
    }

   onOptionsSelectedType(e)
  {
  		console.log(e);
  }

  modalClose()
  {
    this.viewCtrl.dismiss();
  }

}



