import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { HttpClientModule } from '@angular/common/http'; 

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { DealerformPage } from '../pages/dealerform/dealerform';
import { CustomerformPage } from '../pages/customerform/customerform';
import { VehicleformPage } from '../pages/vehicleform/vehicleform';
import { DealerCustomerformPage } from '../pages/dealer-customerform/dealer-customerform';
import { CustomerdetailsPage } from '../pages/customerdetails/customerdetails';
import { CustomersummaryPage } from '../pages/customersummary/customersummary';
import { VehiclesummaryPage } from '../pages/vehiclesummary/vehiclesummary';
import { PaymentmodeSummaryPage } from '../pages/paymentmode-summary/paymentmode-summary';
import { ReportPage } from '../pages/report/report';
import { AdminloginPage } from '../pages/adminlogin/adminlogin';
import { DealerhomePage } from '../pages/dealerhome/dealerhome';
import { DealerprofilePage } from '../pages/dealerprofile/dealerprofile';
import { DealerpaymentPage } from '../pages/dealerpayment/dealerpayment';
import { DealerslistPage } from '../pages/dealerslist/dealerslist';
import { CustomerslistPage } from '../pages/customerslist/customerslist';
import { VehicledetailsPage } from '../pages/vehicledetails/vehicledetails';
import { LoginPage } from '../pages/loginpage/loginpage';
import { DealerscanPage } from '../pages/dealerscan/dealerscan';
import { DealersummaryPage } from '../pages/dealersummary/dealersummary';


import { Network } from '@ionic-native/network';
import { File } from '@ionic-native/file';
import { FileOpener } from '@ionic-native/file-opener';
import { DomSanitizer } from '@angular/platform-browser';
import { Serial } from '@ionic-native/serial';
import { Camera } from '@ionic-native/camera';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { BasicoperationProvider } from '../providers/basicoperation/basicoperation';
import { HttpProvider } from '../providers/httpprovider/httpprovider';
import { CameraProvider } from '../providers/camera/camera';
import { SocialSharing } from '@ionic-native/social-sharing';
import { SocialShareProvider } from '../providers/social-share/social-share';
import { IonicStorageModule } from '@ionic/storage';
import { ActionSheet , ActionSheetOptions } from '@ionic-native/action-sheet';
import { ControllersProvider } from '../providers/controllers/controllers';
import { FileTransfer} from '@ionic-native/file-transfer';
import { AndroidPermissions } from '@ionic-native/android-permissions';
import { NetworkProvider } from '../providers/network/network';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    DealerformPage,
    CustomerformPage,
    VehicleformPage,
    DealerCustomerformPage,
    CustomerdetailsPage,
    CustomersummaryPage,
    VehiclesummaryPage,
    PaymentmodeSummaryPage,
    ReportPage,
    AdminloginPage,
    DealerhomePage,
    DealerprofilePage,
    DealerpaymentPage,
    DealerslistPage,
    CustomerslistPage,
    VehicledetailsPage,
    LoginPage,
    DealerscanPage,
    DealersummaryPage
  ],
  imports: [
  HttpClientModule,
    BrowserModule,
    IonicStorageModule.forRoot(),
    IonicModule.forRoot(MyApp),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    DealerformPage,
    CustomerformPage,
    VehicleformPage,
    DealerCustomerformPage,
    CustomerdetailsPage,
    CustomersummaryPage,
    VehiclesummaryPage,
    PaymentmodeSummaryPage,
    ReportPage,
    AdminloginPage,
    DealerhomePage,
    DealerprofilePage,
    DealerpaymentPage,
    DealerslistPage,
    CustomerslistPage,
    VehicledetailsPage,
    LoginPage,
    DealerscanPage,
    DealersummaryPage
      ],
  providers: [
    Network,
    Serial,
    Camera,
    ActionSheet,
    AndroidPermissions,
    FileTransfer,
    StatusBar,
    SocialSharing,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    BasicoperationProvider,
    HttpProvider,
    CameraProvider,
    SocialShareProvider,
    ControllersProvider,
    File,
    FileOpener,
    NetworkProvider
  ]
})
export class AppModule {}



