import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams , Platform} from 'ionic-angular';
import { BasicoperationProvider } from '../../providers/basicoperation/basicoperation';
import { HttpProvider } from '../../providers/httpprovider/httpprovider';
import { VehicledetailsPage } from '../../pages/vehicledetails/vehicledetails';
import { CustomerformPage } from '../../pages/customerform/customerform';

import { ModalController } from 'ionic-angular';

import pdfMake from 'pdfmake/build/pdfmake';
import pdfFonts from 'pdfmake/build/vfs_fonts';
pdfMake.vfs = pdfFonts.pdfMake.vfs;
 
import { File } from '@ionic-native/file';
import { FileOpener } from '@ionic-native/file-opener';

@Component({
  selector: 'page-customerslist',
  templateUrl: 'customerslist.html',
})
export class CustomerslistPage {

	public customerApi = "wp-json/wp/v2/customer";
  public dealerPostApi = "wp-json/wp/v2/dealer_posts";
  public dealerApi = "wp-json/wp/v2/dealer";
	public customerinfo : any = [];
  public dealerInfo : any= [];
  public dealerData : any = [];
  public dealerSelect : boolean = false;
	pdfObj = null;
  public all : any = [];
  public dealerid : any;
	public rfid :any;
  public checkDealerPage : boolean = false;

  constructor(public navCtrl: NavController,
   public navParams: NavParams,
   public httpreq : HttpProvider,
   public basic_op : BasicoperationProvider,
   private file: File,
    private fileOpener: FileOpener,
     private plt: Platform,
     public modalCtrl: ModalController) {
   	this.customerApi = this.httpreq.getUrl() + this.customerApi;
     this.dealerPostApi = this.httpreq.getUrl() + this.dealerPostApi;
     this.dealerApi = this.httpreq.getUrl() + this.dealerApi;
  }

  ionViewDidLoad() {

    this.rfid =  this.navParams.get("rfid");
   if(this.rfid != undefined)
   {
     this.checkDealerPage = true;
     this.customerDetails(this.rfid);
   }
   else{
     console.log(this.rfid);
     this.getAllDealers();
   }
    
  }

  getAllDealers()
  {
     
    this.httpreq.getAllDataPosts(this.dealerPostApi).then(res=>{
      this.dealerInfo = res;
      for(let i=0;i<this.dealerInfo.data.length;i++)
      {
          this.dealerData[i] = this.dealerInfo.data[i].acf.mobile_no;
      }
        this.all = this.dealerData;
        console.log(this.all);
      return this.dealerData;
    },err=>{
      this.basic_op.alertboxDismiss(err.message);
    });

  }

  getItems(ev) {
    this.customerinfo = [];
     let val = ev.target.value;

     if(val != 0)
     {
         if (val && val.trim() != '') {
           this.dealerSelect = true;
      this.dealerData = this.all.filter((dealer) => {
        return (dealer.indexOf(val) > -1);
      })
          }
     }
     else{
       this.dealerSelect = false;
         this.dealerData = this.all;
     }
 }

    openItem(mobileno)
    {
      this.dealerSelect = false;

      this.basic_op.loaderStart();
        this.httpreq.filterAcfFields(this.dealerApi,'mobile_no',mobileno).then(res=>{
         this.dealerid = res;
         this.rfid = this.dealerid[0].acf.rfid_no;
         this.dealerid = this.dealerid[0].id;
         
         console.log("pp" + this.rfid);
        this.httpreq.filterAcfFields(this.customerApi,'dealer_id',this.dealerid,10).then(res=>{
            this.basic_op.loaderEnd(res);
            this.customerinfo = res;           
            console.log(this.customerinfo);
        },err=>{
            this.basic_op.loaderEnd(err);
            this.basic_op.alertboxDismiss(err.message);
            });
        });

     // this.rfid = rfid;
     // this.customerDetails(rfid);
    }

  customerDetails(rfid)
  	{
        this.basic_op.loaderStart();
        this.httpreq.filterAcfFields(this.dealerApi,'rfid_no',rfid).then(res=>{
         this.dealerid = res;
         this.dealerid = this.dealerid[0].id;
        this.httpreq.filterAcfFields(this.customerApi,'dealer_id',this.dealerid,99).then(res=>{
            this.basic_op.loaderEnd(res);
            this.customerinfo = res;           
            console.log(this.customerinfo);
        },err=>{
            this.basic_op.loaderEnd(err);
            this.basic_op.alertboxDismiss(err.message);
            });
        });
  	}

  	vehicleDetails(customerid)
    {
        this.navCtrl.push(VehicledetailsPage, {customerId: customerid,rfid:this.rfid});
         
    }

    editCustomer(customerId)
    {
      console.log(this.dealerid);
      let customer = this.modalCtrl.create(CustomerformPage, {customer_id:customerId,dealerid:this.dealerid });
       
       customer.onDidDismiss(()=>{
        this.customerDetails(this.rfid);
       });
       customer.present();
    }

    reportPage(customerInfo)
    {
          this.downloadPdf(customerInfo);
    }

    downloadPdf(customerInfo)
    {
        this.createPdf(customerInfo);

        if (this.plt.is('cordova')) {
      this.pdfObj.getBuffer((buffer) => {
        var blob = new Blob([buffer], { type: 'application/pdf' });
 
        // Save the PDF to the data Directory of our App
        this.file.writeFile(this.file.dataDirectory,customerInfo.acf.customer_name+'.pdf', blob, { replace: true }).then(fileEntry => {
          // Open the PDf with the correct OS tools
          this.fileOpener.open(this.file.dataDirectory+customerInfo.acf.customer_name+'.pdf', 'application/pdf');
        })
      });
    } else {
      // On a browser simply use download!
      this.pdfObj.download();
    }
    }

    createPdf(customerInfo) {

      console.log(customerInfo);
   
    var docDefinition = {
      content: [

        { text: 'Customer Details', style: 'header' },
        { text: 'Customer Name', style: 'label' },
        { text: customerInfo.acf.customer_name , style: 'subheader'},
        { text: 'Area', style: 'label' },
        { text: customerInfo.acf.area , style: 'subheader'},
        { text: 'Address', style: 'label' },
        { text:  customerInfo.acf.address , style: 'subheader'},
        { text: 'Mobile No', style: 'label' },
        { text: customerInfo.acf.mobile_no , style: 'subheader'},
        
      ],
      styles: {
        header: {
          fontSize: 15,
          bold: true,
        },
        subheader: {
          fontSize: 11,
          bold: true,
          alignment: 'right',
        },
        label: {
          fontSize: 10,
          alignment: 'left'
        },
      }
    }
    this.pdfObj = pdfMake.createPdf(docDefinition);
  }

}



